@import url('https://fonts.googleapis.com/css?family=Spectral+SC');
.home-container {
  background-color: rgb(49, 42, 42);
  color: rgb(255, 156, 16);
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.toggle-header { /*upper tabs*/
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;

}
.toggle-header * { /* every tab of upper tab */
  width: 50vw;
  border: solid rgb(255, 156, 16) .3vw;
  background-color: rgb(49, 42, 42);
  color: rgb(255, 156, 16);
}

.join-room-container {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  height: 75%;
}
.join-room-input {
  text-align: center;
}

.join-room-submit {
  margin-top: 2vh;
  width: 50vw;
  border: outset rgb(255, 156, 16) .3vw;
}



